import { useState } from 'react'
import Header from "./components/Header"
import Employees from "./components/Employees"
import AddEmployee from "./components/AddEmployee"

const App = () => {
  const [showAddEmployee, setShowAddEmployee] = useState(false)
  // const[showDeleteEmployee, setShowDeleteEmployee] = useState(true)
  const [employees, setEmployees] = useState ([
    {
        id: 1,
        text: "Avădănei Constantin",
        ocupation: "Manager",
        day: "December 5, '99",
        star: true,
    },
    {
        id: 2,
        text: "Bruce Pills",
        ocupation: "Hard Worker",
        day: "June 5, '77",
        star: true,
    }, 
    {
    //     id: 3,
    //     text: "Lady Rața",
    //     ocupation: "Cântă la ușă",
    //     day: "May 16, 2020",
    //     star: false,
    // },
    id: 3,
    text: "Brad Kitt",
    ocupation: "Service staff",
    day: "May 16, 2020",
    star: false,
    },
    {
      id: 4,
      text: "Jean-Claude Pandanne",
      ocupation: "Kitchen security ",
      day: "June 22, '88",
      star: true,
  },
])
//toogle Star
const toggleStar = (id) => {
  setEmployees(employees.map((employee) => 
  employee.id === id 
  ? 
  {...employee, star: !employee.star} : employee))
}
// add employee
const addEmployee = (employee) => {
  const id = Math.floor(Math.random() * 10000) + 1
  const newEmployee = { id, ...employee }
  setEmployees([...employees, newEmployee])
}

//delete employee
const deleteEmployee = (id)  => {
  setEmployees(employees.filter((employee) => employee.id !== id))
}

  return (
    <div className="container">
      
      {/* navigation band */}
      <div className ='navBand'>
        <div className ='wrapperDivLogo'>
          <div className ='divLogo'></div>
        </div>
        <p className = 'brand-logoSky'>Sky Acceron</p>

        <ul className = 'rightNav'>
          <li>Contact</li>
          <li>Employees</li>
          <li>Home</li>
        </ul>
      </div>


     <Header onAdd = {() => 
     setShowAddEmployee(!showAddEmployee)}    
     showAdd={showAddEmployee}
     />
     {showAddEmployee && <AddEmployee onAdd={addEmployee}/>}

     {employees.length > 0 ? (
     <Employees employees={employees} 
     onDelete={deleteEmployee} 
     onToggle = {toggleStar} 
     />
     ) : (
       <p className='firedText'>You, my frend, fired them all !!</p>
       )}
    </div>
  )
}

export default App
